<template>
  <div class="back">
    <app-bar back-icon title="消息中心" hideMessage hideDemand></app-bar>
    <v-content style="min-height: calc(100vh - 64px)">
      <v-container>
        <v-card class="mx-auto mt-12" min-height="400" max-width="800">
          <div v-if="!messageList">
            <p class="text-center tcenter headline">您暂时还没有新消息哦~</p>
          </div>
          <v-list v-if="messageList" three-line>
            <v-list-item v-for="item in messageList" :key="item.time">
              <v-list-item-avatar>
                <v-img src="../../assets/img/message.png"></v-img>
              </v-list-item-avatar>

              <v-list-item-content>
                <v-list-item-title>系统消息</v-list-item-title>
                <v-list-item-subtitle
                  v-html="item.message"
                ></v-list-item-subtitle>
              </v-list-item-content>
              <v-list-item-action>
                <v-list-item-action-text
                  v-text="item.time"
                ></v-list-item-action-text>
              </v-list-item-action>
            </v-list-item>
          </v-list>
        </v-card>
      </v-container>
    </v-content>
    <app-footer></app-footer>
  </div>
</template>

<script>
import AppBar from '@/components/App/AppBar'
import AppFooter from '@/components/App/AppFooter'

export default {
  name: 'PersonalCenter',
  components: {
    AppBar,
    AppFooter
  },
  data() {
    return {
      messageList: []
    }
  },
  methods: {
    async getMessageList() {
      let res = await this.$http.get('/api/user/getmessage')
      this.messageList = res.data.data
    }
  },
  mounted() {
    this.getMessageList()
  }
}
</script>

<style scoped>
.back {
  background-color: #f2f2f2;
}
.tcenter {
  line-height: 400px;
}
</style>
